<template>
    <div>
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-server m-r-10" aria-hidden="true"></i>{{$t('device.deviceList')}}</p>
        </div>
        <div class="m-t-20">
            <div class="panel-header m-b-20">
                <span>{{$t('label.selectGroup')}}</span>
                <el-select v-model="selectedGroup" :placeholder="$t('device.selectGroupHolder')" @change="selectGroup()" size="small" style="width: 180px" filterable>
                    <el-option value="All">
                    {{$t('global.all')}}
                    </el-option>
                    <el-option
                    v-for="item in groupData"
                    :key="item.name"
                    :label="item.name"
                    :value="item.name">
                        <span style="float: left">
                            <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.windows"></i>
                            <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.android"></i>
                            <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.linux"></i>
                            <i class="fa fa-object-group m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.mix"></i>
                            {{ item.name}}
                        </span>

                    </el-option>
                </el-select>
                <el-button size="small" type="default" @click="groupManager()">
                    <i class="fa fa-object-group m-r-5" aria-hidden="true"></i>
                   {{$t('device.groupManagement')}}
                </el-button>
                <span class="m-l-30">{{$t('label.selectOs')}}</span>
                <el-select v-model="selectedOs"  @change="selectOs()" :placeholder="$t('device.selectOsHolder')" size="small" style="width: 180px">
                    <el-option value="All">
                    {{$t('global.all')}}
                    </el-option>
                    <el-option
                    v-for="item in osArray"
                    :key="item.name"
                    :label="item.name"
                    :value="item.value">
                        <span style="float: left">
                            <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.value== groupTypeArray.windows"></i>
                            <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.value== groupTypeArray.android"></i>
                            <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.value== groupTypeArray.linux"></i>
                            {{ item.name}}
                        </span>

                    </el-option>
                </el-select>
                <div class="fr m-r-10"> 
                    <el-input size="small" class="max-w-300 m-l-10" v-model="keywords" :placeholder="$t('device.keywordHolder')" 
                    @keyup.enter.native="searchDevice(keywords)"
                    @input="searchDevice(keywords)">
                         <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                </div>
            </div>
            <el-table
                id="deviceTable"
                ref="deviceTable"
                :data="deviceList"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="selectItem"
                @filter-change="filterIsOnline"
                :default-sort = "{prop: 'registrationDate', order: 'descending'}"
                v-loading="listLoading" 
                >
                <el-table-column
                type="selection"
                min-width="50">
                <!-- :selectable="checkSelectable"> -->
                </el-table-column>
                <!-- <el-table-column
                prop="endpoint"
                :label="$t('device.agentId')"
                min-width="120"
                sortable>
                </el-table-column> -->
                <el-table-column
                min-width="30">
