<template>
    <el-dialog :visible.sync="vncDialogVisible" @close="initVNC" @open="openVNC" width="80%" top="3vh" :fullscreen="isFullScreen" :close-on-click-modal="false">
        <div slot="title" class="dialog-title">
            <i class="fa fa-desktop m-r-10" aria-hidden="true"></i> 
            {{$t('vnc.remoteDesktop')}}  
            <i class="devname">{{deviceName}}</i>
            <el-tooltip class="item" effect="light" :content="$t('vnc.exitFullscreen')" placement="top-start" v-if="isFullScreen">
                <svg t="1612428267851" class="icon pointer" 
                 @click="isFullScreen = false" 
                viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                p-id="5710" width="21" height="21">
                    <path d="M384 938.666667h42.666667v-341.333334H85.333333v42.666667h298.666667v298.666667z" fill="#378f8c" p-id="5711"></path>
                    <path d="M422.229333 631.466667l-302.037333 301.994666-29.866667-29.866666L392.362667 601.6zM640 426.666667h298.666667V384h-298.666667V85.333333h-42.666667v341.333334h42.666667z" fill="#378f8c" p-id="5712"></path>
                    <path d="M934.869333 119.68l-301.994666 301.994667-29.866667-29.866667 301.994667-302.037333zM426.24 426.666667V85.333333h-42.666667v298.666667h-298.666666v42.666667h341.333333z" fill="#378f8c" p-id="5713"></path>
                    <path d="M392.96 420.992L91.050667 118.997333l29.866666-29.866666L422.826667 391.125333zM640.426667 640.426667h298.666666v-42.666667h-341.333333v341.333333h42.666667v-298.666666z" fill="#378f8c" p-id="5714"></path>
                    <path d="M905.472 935.552l-301.994667-301.994667 29.866667-29.866666 301.994667 301.994666z" fill="#378f8c" p-id="5715"></path>
                </svg>
            </el-tooltip>
            <el-tooltip class="item" effect="light" :content="$t('vnc.fullscreen')" placement="top-start" v-else>
                <svg @click="isFullScreen = true"
                t="1612428295141" class="icon pointer" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                p-id="5915" data-spm-anchor-id="a313x.7781069.0.i5" width="21" height="21">
                    <path d="M922.282667 178.005333v149.333334a25.6 25.6 0 0 1-51.2 0v-149.333334a22.442667 22.442667 0 0 0-22.4-22.4h-149.333334a25.6 25.6 0 1 1 0-51.2h149.333334a73.685333 73.685333 0 0 1 73.6 73.6zM325.802667 102.4h-149.333334a73.685333 73.685333 0 0 0-73.770666 73.6v149.333333a25.6 25.6 0 0 0 51.2 0v-149.333333a22.442667 22.442667 0 0 1 22.4-22.4h149.333333a25.6 25.6 0 1 0 0-51.2z m-1.024 766.037333h-149.333334a22.442667 22.442667 0 0 1-22.4-22.4v-149.333333a25.6 25.6 0 0 0-51.2 0v149.333333a73.685333 73.685333 0 0 0 73.6 73.6h149.333334a25.6 25.6 0 1 0 0-51.2z m570.88-195.498666a25.6 25.6 0 0 0-25.6 25.6v149.333333a22.442667 22.442667 0 0 1-22.4 22.4h-149.333334a25.6 25.6 0 1 0 0 51.2h149.333334a73.685333 73.685333 0 0 0 73.6-73.6v-149.333333a25.6 25.6 0 0 0-25.557334-25.642667z" fill="#378f8c" p-id="5916"></path>
                </svg>
            </el-tooltip>
            
        </div>
        <div class="body text-center">
            <div class="noVNC-box" >
                <div id="noVNC_content">
                    <i class="fa fa-times fa-x pointer" @click="disconnectVNC" id="VNC_closed" v-show="isShowClosed" ></i>
                    <div id="noVNC_status_bar" :class="['noVNC_status_' + level]">
                        <div id="noVNC_left_dummy_elem"></div>
                        <div id="noVNC_status">
                            {{VNCStatus}}
                        </div>

                        <div id="VNC_enter">
                            <div v-show="connectStatus === 'disconnect'">
                                <h2 class="VNC_title">{{$t('vnc.remoteToYourDevice')}}</h2>
                                <el-button type="primary"  @click="connectVncServer()">
                                    <i class="fa fa-link"></i> {{$t('vnc.connect')}}
                                </el-button>
                            </div>
                            <div v-show="connectStatus === 'connecting'">
                                <img src="@/assets/imgs/connect_vnc.gif" alt="no img">
                            </div>
                            <div v-show="connectStatus === 'connected'"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="initVNC">
                {{$t('global.close')}}
            </el-button>
        </div>
    </el-dialog>
</template>
<script>
    // import {getVNCPropertysApi} from '../restfulapi/vncapi'
    import handleResponse from '../restfulapi/handleResponse'
    import selectDevice from '../../common/select-device'
    import * as WebUtil from '../../../static/noVNC/app/webutil.js';
    import RFB from '../../../static/noVNC/core/rfb.js';
     import {getVncServerHostApi,getRepeaterServerApi,getRepeaterViewerApi} from "../restfulapi/serverConfigApi"
    import {setDeviceStatus, execDeviceStatus} from '../restfulapi/deviceStatusApi'
    import {vncServer} from "../../assets/js/lwm2mMap"
    import {repeaterIp} from "../../assets/js/constant"
    export default{
        name: 'vncList',
        data(){
            return {
                selectedAgentId: '',
                deviceName: "",
                rfb: null,
                vncMode: 3,
                repeaterID: 0,
                desktopName:'',
                connectStatus: "disconnect",
                isShowClosed: false,
                level: '',
                VNCStatus: '',
                vncDialogVisible: false,
                isContinue: true,
                isFullScreen: false
            }
        },
        components:{
            selectDevice
        },
        methods: {
            openVNC(){
                this.isContinue = true;
            },
            disconnectVNC(){
                this.isContinue = false;
                setDeviceStatus(this.selectedAgentId, vncServer["stopVncServer"], true)
                this.status("disconnect", "normal", "disconnect");
                if(this.rfb != null){
                    this.rfb._sock.close();
                };
            },

            getRepeaterId(){
                this.repeaterID = Math.floor(Math.random()*9000)+1000;
            },

            separateUrl(url){
                let ip = url.substring(url.indexOf(""))
            },

            connectVncServer(){
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'), "info", {button: this.$t('global.confirm')})
                    return;
                }

                //password must be string 
                let password = Math.floor(Math.random()*9000)+1000+"";
                this.status("connecting", "normal", "connecting");
                window.setTimeout(() => {
                    getVncServerHostApi().then((host) => {
                        let data =  {
                            repeaterid: this.repeaterID, 
                            url: repeaterIp, 
                            password: password
                            }
                        setDeviceStatus(this.selectedAgentId, vncServer["startVncServer"], JSON.stringify(data)).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status = "CHANGED"){
                                    let path = "websockify/?token=00190F2A51F5";
                                    window.setTimeout(() => {
                                        this.startVNC(host, "", password, path);
                                    }, 3000);
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })  
                    }); 
                }, 2000)  
            },

            updateDesktopName(e) {
                this.desktopName = e.detail.name;
            },


            status(text, level, type) {
                var vncContentMsg = ` <h2 class="VNC_title">VNC To Your Device</h2>
                    <el-button type="primary"  @click="startVNC">
                        <i class="fa fa-link"></i> Connect
                    </el-button>`;
                switch (level) {
                    case 'normal':
                        break;
                    case 'warn':
                        break;
                    case 'error':
                        type = "disconnect";
                        break;
                    
                    default:
                        this.level = "warn";
                }
                if(type == "connecting"){
                    this.isShowClosed = false;
                    this.connectStatus = "connecting";
                }else if(type == "disconnect"){
                    this.isShowClosed = false;
                    this.connectStatus = "disconnect"
                }else{
                    this.isShowClosed = "true";
                    this.connectStatus="connected"
                }
                this.level = level;
                this.VNCStatus = text;
            },

            connected(e) {
                if (WebUtil.getConfigVar('encrypt',true)) {
                    this.status("Connected (encrypted) to " + this.desktopName, "normal", "connected");
                } else {
                    this.status("Connected (unencrypted) to " + this.desktopName, "normal", "connected");
                }
            },

            disconnected(e) {
                if (e.detail.clean) {
                    this.status("disconnect", "normal", "disconnect");
                } else {
                    this.status("Something went wrong, Try connecting again later", "error", "error");
                }  
            },



            // By default, use the host and port of server that served this fil

                
            startVNC(host, port, password, path){
                if(!this.isContinue){
                    this.status("disconnect", "normal", "disconnect");
                    console.error("Not start VNC");
                    return;
                }
                var token = WebUtil.getConfigVar('token', null);
                if (token) {
                    // if token is already present in the path we should use it
                    path = WebUtil.injectParamIfMissing(path, "token", token);
                
                    WebUtil.createCookie('token', token, 1)
                }

                this.status("connecting", "normal", "connecting");
                if ((!host) || (!port)) {
                    this.status('Must specify host and port in URL', 'error', "error");
                    return;
                }

                var url  = 'wss';

                url += '://' + host;
                if(port) {
                    url += ':' + port;
                }
                url += '/' + path;

                this.rfb = new RFB(document.getElementById('noVNC_content'), url,
                    { repeaterID: this.repeaterID,
                        shared: true,
                        credentials: { password: password } });
                this.rfb.viewOnly = WebUtil.getConfigVar('view_only', false);
                this.rfb.addEventListener("connect",  this.connected);
                this.rfb.addEventListener("disconnect", this.disconnected);
                this.rfb.addEventListener("desktopname", this.updateDesktopName);
                this.rfb.scaleViewport = true;
                this.rfb.resizeSession = WebUtil.getConfigVar('resize', false);
            },

            getDeviceOption(msg){
                if(this.selectedAgentId){
                    this.disconnectVNC();
                }
                this.selectedAgentId = msg;
            },

            initVNC(){
                this.vncDialogVisible = false;
                this.isFullScreen= false
                this.deviceName = "";
                this.disconnectVNC();
                this.selectedAgentId = "";
            }
        },
        
        created(){
            this.getRepeaterId();
        },

        beforeDestroy() {
            this.disconnectVNC();
            this.selectedAgentId = "";
        },
        
    }
</script>
<style lang='scss' scoped>
@import "../../assets/css/colors"; 
#noVNC_content{
    border:1px solid #ddd;
    height:75vh;
    position:relative;
    background-color:#eaeaea;
    margin-right: 10px;
    overflow: hidden;
}

#VNC_closed{
    position: absolute;
    color:#d3104b;
    padding:5px;
    right:1px;
    top:-4px;
    cursor: pointer;
}

#noVNC_status_bar {
    width: 100%;
    text-align: center;
}

#noVNC_status {
    color: #fff;
    font: bold 12px Helvetica;
    margin: auto;
}

.noVNC_status_normal {
    background: linear-gradient(#b2bdcd 0%,#899cb3 49%,#7e93af 51%,#6e84a3 100%);
}

.noVNC_status_error {
    background: linear-gradient(#c83737 0%,#899cb3 49%,#7e93af 51%,#6e84a3 100%);
}

.noVNC_status_warn {
    background: linear-gradient(#b4b41e 0%,#899cb3 49%,#7e93af 51%,#6e84a3 100%);
}

.noNVC_shown {
    display: inline;
}
.noVNC_hidden {
    display: none;
}

#noVNC_left_dummy_elem {
     flex: 1;
}
#Terminal_enter .Terminal_title, #VNC_enter .VNC_title{
    color:#337ab7;
    margin-bottom:20px;
}

#VNC_enter, #Terminal_enter{
    position: absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    text-align: center;
}

.content{
    display: flex;
    flex-direction: column;
    .title-box{
        margin:10px;
        // background-color: #fff;
        border: 5px;
        flex: 0 1 auto;
    }
    
    .noVNC-box{
        flex: 1 1 auto;
        margin:10px;
        background-color: #fff;
        border: 5px;
    }
} 
.devname{
    font-size: 14px;
    margin-left: 10px;
}
.dialog-title{
    svg{
        position: relative;
        top: 5px;
        margin-left: 5px;
    }
}
</style>


